*{
    font-family: Arial, Helvetica, sans-serif;
}

body{
    margin: 1em;
}

p{
    font-family: Helvetica, sans-serif;
    font-size: 16px;
}

span{
    font-size: 10px;
    text-decoration: none !important;
}

section{
    margin: 0.25em;
}

.item{
    color: black;
    font-size: 12px;
    display: inline-block;
    width: 100%;
    border: black 1px solid;
    border-radius: 16px;
    margin: 10px 0;
    transition: height, 200ms, ease-in;
}

span{
    font-size: 24px;
    margin-left: 10px;
    border: 1px solid black;
    padding: 7px;
    border-radius: 16px;
}

#icon{
    width: 30px;
    border-radius: 0;
    display: inline;
    position: relative;
    top: 5px;
}

.item div{
    margin: 1em;
}

h1{
    font-size: 25px;
}

#scavListTitle{
    margin-top: 50px;
    text-align: center;
}

h2 {
    font-weight: bolder;
    font-size: 25px;
}

.task{
    margin: 0;
    display: inline-block;
}

img{
    display: block;
    width: 100%;
    margin-top: 15px;
    border-radius: 16px;
}

.locked{
    background-color: rgb(59, 59, 59);
    color: rgb(200, 200, 200);
}

#lookingFor{
    font-size: 24px;
}

.button{
    display: block;
    margin-top: 15px;
    margin: 15px;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    border-radius: 16;
    color: white;
    padding: 20px;
    background-color: rgb(41, 41, 41);
}

#cam{
    height: 750px;
    object-fit: cover;
}

#photoButton{
    display: block;
    margin-top: 15px;
    margin: 15px;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    border-radius: 16;
    color: rgb(41, 41, 41);
    padding: 20px;
    border: solid 2px rgb(41, 41, 41);
}

video{
    width: 100%;
}

footer{
    background-color: white;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
}

